import React,{useState,useEffect} from 'react'
import './restapi.css'
import axios from 'axios'
import Paginationcontainer from './Paginationcontainer'


function RestAPI() {

    const [getcategory,setCategory]=useState([])


    const getData= async ()=>{
        await axios.get('https://testing.pogo91.com/api/online-store/category/?format=json&store_prefix=cake-shop')
        .then(res=>{
            // console.log(res.data.category)
            setCategory(res.data.category)
            return res.data.category
        })
        .catch(err=>{
            console.log(err)
        })
    }



    useEffect(()=>{
        getData()

        if(getData) setCategory(getData)

        setCategory(0)
        
    },[])

    // console.log(getcategory)

    // const arr=Object.keys(getcategory).map((key,index)=>
    // [getcategory[index]]
    // )

    return (
        <div className="category-header">
            categories

            <div className="container">
                
                 <div className="category-list">
        {getcategory && getcategory.map((key,item)=>
            <ul key={getcategory[key]}>{getcategory[item].name}</ul>
        )}
        </div>
            <Paginationcontainer />

            </div>

        </div>
    )
}

export default RestAPI
