import React,{useState,useEffect} from 'react'
import BasicPagination from './BasicPagination'
import axios from 'axios'
import './paginationcontainer.css'
// import ReactPaginate from 'react-paginate'


function Paginationcontainer() {
    const [products,setProducts]=useState([])
    const [pageNumber,setPageNumber]=useState(0)

    const [page, setPage] = useState(1);
    

    let newArray=[]

    useEffect(()=>{
        getData()
        paginate(page)
       
      },[])

      products.map((key,item)=>{
        //  console.log(products[item].product_name)
         newArray.push({ProductName:products[item].product_name,image_URl:products[item].image_url,
          MRP:products[item].price_stock[0]})
        // setNewArray(arr=>[...products,products[item].name])
        })
      
      const getData= ()=>{
        axios.get('https://testing.pogo91.com/api/online-store/category/product/?category_id=0&format=json&page=1&store_prefix=cake-shop')
        .then(res=>{
            console.log(res)
            // setProducts(res)
            setProducts(res.data.products)
            return res.data.products
        })
        .catch(err=>{
            console.log(err)
            
        })
      }

  

      function paginate(n){
        let x=(n*2-2)*2
        let a=newArray.slice(x,x+4)
        let c=Object.entries(a)
        return c.map((key,item)=>{
          // console.log(c[item][1].ProductName,c[item][1].image_URl,c[item][1].ProductName,c[item][1].MRP.mrp,
          //   c[item][1].MRP.selling_price,c[item][1].MRP.variant_id)
  
            return(
              <div className="mmmmm">
                <img src={c[item][1].image_URl} alt=""/>
                <div className="b">
                <label>Product Name:{c[item][1].ProductName}</label>
                <label>MRP:{c[item][1].MRP.mrp}</label>
                <label>selling:{c[item][1].MRP.selling_price}</label>
                <label>variant ID:{c[item][1].MRP.variant_id}</label>
                </div>

             {/* <label>
              Product Name:{c[item][1].ProductName}{<br/>}
              image url:{c[item][1].image_URl}<br/>
              MRP:{c[item][1].MRP.mrp}<br/>selling:{c[item][1].MRP.selling_price}<br/>variant ID:{c[item][1].MRP.variant_id}<br/>        
              </label> */}
              </div>
     
          )
             } )
      }

      

      const dataPerpage=4
      const pagesVisited=pageNumber * dataPerpage
      const PageCount=Math.ceil(newArray.length/dataPerpage)
      // const displayData=newArray.slice(pagesVisited,pagesVisited+dataPerpage).map((key,item)=>{
      //   //   console.log(newArray[item].ProductName)
      //   return(
      //       <label>
      //       Product Name:{newArray[item].ProductName}{<br/>}image url:{newArray[item].image_URl}<br/>        
      //   </label>
      //   )
   
      // })


// console.log(displayData)
// console.log(newArray)
      const handleChange=(event,value)=>{
        setPage(value)
        
      }
    return (
        <div className="pagination-container">
          <div className="a">
          {paginate(page)}
          </div>
        
        <div className="s"> <BasicPagination count={PageCount} page={page} onChange={handleChange} /></div>
    </div>
    )
}

export default Paginationcontainer
